import React from 'react'
import { Grid } from 'antd-mobile';

const data = Array.from(new Array(9)).map((_val, i) => ({
  icon: 'https://i.52112.com/icon/jpg/256/20190517/39786/1878536.jpg',
  text: `name${i}`,
}));

const data1 = Array.from(new Array(9)).map(() => ({
  icon: 'https://i.52112.com/icon/jpg/256/20171017/3751/198405.jpg',
}));


const ZHlist = () => (
  <div>
    <div className="sub-title">服务中心</div>
    <Grid data={data} activeStyle={false} />
    <div className="sub-title">购物中心</div>
    <Grid data={data1}
      columnNum={3}
      renderItem={dataItem => (
        <div style={{ padding: '12.5px' }}>
          <img src={dataItem.icon} style={{ width: '75px', height: '75px' }} alt="" />
          <div style={{ color: '#888', fontSize: '14px', marginTop: '12px' }}>
            <span>I am title..</span>
          </div>
        </div>
      )}
    />
    <div className="sub-title">秒杀专场</div>
    <Grid data={data1} columnNum={3} itemStyle={{ height: '150px', background: 'rgba(0,0,0,.05)' }} />
  </div>
);
export default ZHlist;
